:root {
  --font-body: "Raleway", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --pale-blue: hsl(243, 100%, 93%);
  --grayish-blue: hsl(229, 7%, 55%);
  --dark-blue: hsl(228, 56%, 26%);
  --very-dk-blue: hsl(229, 57%, 11%) ;
}

html {
  font-size: 100%;
  box-sizing: border-box;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 0.875rem;
  font-weight: 400;
  color: var(--pale-blue);
  height: 100vh;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  background-color: var(--very-dk-blue);
  background-image: url("/images/bg-mobile.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
}
@media (min-width: 40em) {
  body {
    background-image: url("/images/bg-desktop.png");
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: 100% 50%;
  }
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 2.25rem;
  letter-spacing: 0.125rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 3.125rem;
  max-width: 57.5rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.component {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.9375rem;
}
@media (min-width: 40em) {
  .component {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 1.875rem;
  }
}

.storage {
  background-color: var(--dark-blue);
  border-radius: 0.625rem 6.25rem 0.625rem 0.625rem;
  padding: 2.5rem;
  flex: 1;
}
@media (min-width: 40em) {
  .storage {
    max-width: 21.875rem;
    padding: 2.625rem;
  }
}
.storage__logo {
  width: 8.75rem;
  height: 2.5rem;
  margin-bottom: 2rem;
}
.storage__link {
  display: flex;
}
.storage__link :not(:last-child) {
  margin-right: 1.0625rem;
}
.storage__link a {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--very-dk-blue);
  width: 3.125rem;
  height: 3.125rem;
  border-radius: 0.625rem;
}
.storage__link--doc {
  width: 1.3125rem;
  height: 1.625rem;
}
.storage__link--folder {
  width: 1.5625rem;
  height: 1.3125rem;
}
.storage__link--upload {
  width: 1.5625rem;
  height: 1.125rem;
}

.data {
  padding: 2.25rem 2rem 3.4375rem;
  position: relative;
  background-color: var(--dark-blue);
  text-align: center;
  border-radius: 0.625rem 0.625rem 0.625rem 0.625rem;
  flex: 1;
}
@media (min-width: 40em) {
  .data {
    align-self: flex-end;
    max-height: 9.6875rem;
    padding: 2.5rem;
  }
}
.data__content {
  margin-bottom: 1.25rem;
}
.data__content span {
  font-weight: 700;
  color: var(--white);
}
@media (min-width: 40em) {
  .data__content {
    text-align: center;
  }
}
@media (min-width: 71.875em) {
  .data__content {
    text-align: left;
  }
}
.data__meter {
  margin-bottom: 0.8125rem;
  height: 1.75rem;
  width: 100%;
  border-radius: 1.25rem;
  -moz-appearance: none;
  background: none;
  background-color: var(--very-dk-blue);
  padding: 0.3125rem;
}
.data__meter::-moz-meter-bar {
  background-image: linear-gradient(to right, #ffa3b3, #e75581);
  background-size: 100% 100%;
  border-radius: 0.625rem;
  border: none;
}
.data__meter::-webkit-meter-bar {
  background: none;
  /* Required to get rid of the default background property */
  background-color: var(--very-dk-blue);
  border: none;
}
.data__meter::-webkit-meter-optimum-value {
  background-image: linear-gradient(to right, #ffa3b3, #e75581);
  background-size: 100% 100%;
  border-radius: 0.625rem;
  border: none;
}
.data__meter-gauge {
  margin-bottom: 0.8125rem;
  height: 1.75rem;
  width: 100%;
  border-radius: 1.25rem;
  background-color: var(--very-dk-blue);
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0.3125rem;
}
.data__meter-gauge span {
  height: 100%;
  width: 82.5%;
  background-image: linear-gradient(to right, #ffa3b3, #e75581);
  background-size: 100% 100%;
  border-radius: 0.625rem;
  border: none;
  font-weight: 700;
}
.data p {
  text-align: left;
  color: var(--white);
  color: var(--white);
  font-weight: 700;
  margin: 0;
}
.data p span {
  float: right;
}
.data__number {
  position: absolute;
  display: flex;
  justify-items: center;
  align-items: center;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 0.625rem 0.625rem 0.625rem 0.625rem;
  padding: 1.0625rem 1.75rem;
  background-color: var(--white);
  color: var(--grayish-blue);
  font-weight: 700;
  text-transform: uppercase;
}
.data__number span {
  color: var(--very-dk-blue);
  font-size: 2.1875rem;
  font-weight: 700;
  margin-right: 0.3125rem;
}
@media (min-width: 40em) {
  .data__number {
    border-radius: 0.625rem 0.625rem 0.625rem 0.625rem;
    top: 0;
    left: 50%;
    right: 0;
    bottom: 50%;
    transform: translate(-50%, -70%);
    width: fit-content;
  }
  .data__number::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    border-width: 0.625rem;
    border-style: solid;
    border-color: var(--white) transparent transparent transparent;
  }
}
@media (min-width: 71.875em) {
  .data__number {
    border-radius: 0.625rem 0.625rem 0rem 0.625rem;
    top: 0;
    left: 60%;
    right: 0;
    bottom: 50%;
    width: fit-content;
    transform: translate(0%, -70%);
  }
  .data__number::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 100%;
    transform: translateX(-100%);
    border-width: 0.625rem;
    border-style: solid;
    border-color: var(--white) var(--white) transparent transparent;
  }
}